import { SymbolKind, Uri } from "vscode";
import {
  resolveWorkspaceSymbols,
  SymbolInformationResult,
} from "../handlers/resolveSymbols";
import { toWorkspaceSymbol, WorkspaceSymbol } from "./symbols";

/**
 * Returns a list of symbols in the workspace matching the given query.
 *
 * @param {string} query - The search query.
 * @param {number} maxNumOfResults - The maximum number of results to return.
 * @param {Uri} fileUri - The file URI.
 * @returns {Promise<{ documentSymbols: WorkspaceSymbol[]; workspaceSymbols: WorkspaceSymbol[] }>} - An object containing the document symbols and workspace symbols.
 */
export async function getSymbolsCommandExecutor(
  query: string,
  maxNumOfResults: number,
  fileUri: Uri
): Promise<{
  documentSymbols: WorkspaceSymbol[];
  workspaceSymbols: WorkspaceSymbol[];
}> {
  const allSymbols: SymbolInformationResult[] =
    (await resolveWorkspaceSymbols({ symbol: query }))
      ?.filter(symbolKindIsAllowed)
      .sort() || [];
  const documentSymbols = allSymbols
    .filter(
      (workspaceSymbol) =>
        workspaceSymbol.location.uri.fsPath === fileUri.fsPath
    )
    .splice(0, maxNumOfResults)
    .map(toWorkspaceSymbol);
  const numOfWorkspaceSymbols = maxNumOfResults - documentSymbols?.length;
  if (numOfWorkspaceSymbols <= 0) {
    return {
      documentSymbols,
      workspaceSymbols: [],
    };
  }
  const workspaceSymbols: WorkspaceSymbol[] =
    allSymbols
      .filter(
        (workspaceSymbol) =>
          workspaceSymbol.location.uri.fsPath !== fileUri.fsPath
      )
      .splice(0, numOfWorkspaceSymbols)
      .map(toWorkspaceSymbol) || [];
  return {
    documentSymbols,
    workspaceSymbols,
  };
}

function symbolKindIsAllowed(symbol: SymbolInformationResult) {
  return [SymbolKind.Method, SymbolKind.Class, SymbolKind.Function].includes(
    symbol.kind
  );
}
